@import "../../global.scss";


.intro{
     background-color: whitesmoke;
     display: flex;

     @include mobile{
          flex-direction: column;
          align-items: center;
        }

     .left{
          flex: 1;
          overflow: hidden;
          
          .imgContainer{
               width: 700px;
               height: 700px;
               background-color: rgb(96, 96, 229);
               border-radius: 50%;
               display: flex;
               align-items: center;
               justify-content: center;
               float: right;

               @include mobile{
                    align-items: flex-start;
                  }


               img{
                    height: 100%;
                    @include mobile{
                         height: 50%;
                       }
               }
          }
          
     }

     .right{
          flex: 1;
          position: relative;

          .wrapper{
               width: 100%;
               height: 100%;
               padding-left: 50px;
               display: flex;
               flex-direction: column;
               justify-content: center;

               @include mobile{
                    padding-left: 0;
                    align-items: center;
                  }

               h1{
                    font-size: 40px;
                    margin: 10px 0;

                    @include mobile{
                         font-size: 40px;
                       }
               }
            
               h2{
                    font-size: 25px;

               }
               h3{
                    font-size: 20px;

                    @include mobile{
                         font-size: 20px;
                       }

                    span{
                         font-size: inherit;
                         color: indianred;
                    }

                    .ityped-cursor{
                         animation: blink 1s infinite;
                    }

                    @keyframes blink {
                         50%{
                              opacity: 1;
                         }
                         100%{
                              opacity: 0;
                         }
                         
                    }
               }
          }
         
          a{
               position: absolute;
               bottom: 10px;
               left: 40%;

               img{
                    width: 30px;
                    height: 30px;
                    animation: arrowBlink 2s infinite;
               }

               @keyframes arrowBlink {
                    to{
                         opacity: 0;
                    }
               }
          }
     }
}