@import "../../global.scss";

.contact{
     background-color: whitesmoke;
     display: flex;
     @import "../../global.scss";

     .left{
          flex: 1;
          overflow: hidden;
          img {
               height: 100%;
               @include mobile {
                width: 200px;
                height: 100%;
              }
             }
     }

     .right{
          flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
     font-size: 30px;
      }
     form{
          width: 70%;
         height: 70%;
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: space-around;
        

         input{
          width: 300px;
          height: 30px;
          font-size: 14px;

          @include mobile {
            width: 120px;
            height: 20px;
            font-size: 12px;
          }
         }

         textarea{
          width: 300px;
          height: 200px;
          font-size: 14px;
           
          @include mobile {
            width: 120px;
            height: 100px;
            font-size: 12px;
          }
         }

         button {
          width: 150px;
          height: 30px;
          color: white;
          background-color: rgb(96, 96, 229);
          border: none;
          border-radius: 10px;
          font-weight: 500;
          cursor: pointer;
  
          &:focus {
            outline: none;
          }
        }

        span {
          color: green;
        }
     }
  }
}